export const HeadSeo = ({
  title,
  description,
  img,
}: {
  title: string;
  description?: string;
  img?: string;
}) => {
  return (
    <>
      <title>{title}</title>
      <meta property="description" content={description ?? title} />
      <meta property="og:image" content={img ?? "/icons/icon-512x512.png"} />
      <link
        rel="apple-touch-icon"
        sizes="180x180"
        href="/apple-touch-icon.png"
      />
      <link
        rel="icon"
        type="image/png"
        sizes="32x32"
        href="/favicon-32x32.png"
      />
      <link
        rel="icon"
        type="image/png"
        sizes="16x16"
        href="/favicon-16x16.png"
      />
      <link rel="manifest" href="/site.webmanifest" />
      <link rel="mask-icon" href="/safari-pinned-tab.svg" color="#5bbad5" />
      <meta name="msapplication-TileColor" content="#da532c" />
      <meta name="theme-color" content="#ffffff" />
    </>
  );
};

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "common" } } }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        fields {
          language
        }
        frontmatter {
          layout {
            requisites {
              ein
              companyCode
              street
              city
              postalCode
              country
              phone
            }
          }
        }
      }
    }
  }
`;
